* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
  color: rgb(219, 215, 219);
  background-color: rgb(27, 27, 27);
  font-size: 14px;
}
.container {
  width: min(400px, 100%);
  margin: auto;
  background-color: rgb(53, 51, 53);
  padding: 20px;
  border-radius: 5px;
}
h1 {
  text-align: center;
  margin-bottom: 20px;
}
form {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
input {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 5px;
  color: inherit;
  width: 135px;
}
input:focus {
  outline: none;
}
input:focus::placeholder {
  color: transparent;
}
.toggle-degree {
  cursor: pointer;
}
.display {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.temp-icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 50px;
}
.city-wrapper {
  font-size: 20px;
}
